@import "../../../styles/colors";
@import "../../../styles/mixins";

.movie-details-container {
  position: relative;
  min-height: 100vh;
  display: flex;
  padding: 20px;

  @include for-phone-only{
    padding: 10px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    background-color: $dark-background;
  }

  &__main {
    position: relative;
    color: $color;
    margin-top: 10rem;
    padding: 0 6rem;

    @include for-phone-only{
      margin-top: 1rem;
      padding: 0 .2rem;
      width: 100%;
    }

    &-content {
      display: grid;
      grid-column: 1;
      gap: 1rem;

      @include for-phone-only{
        display: flex;
        flex-direction: column;
      }
    }
    .column {
      @include for-phone-only{
        display: flex;
        flex-direction: column;
      }
      .title {
        font-size: 2.5rem;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.5rem;
        margin: 1rem 0;
        color: $primary;
        @include for-phone-only{
          font-size: 1.5rem;
          margin: .5rem 0;
        }
      }
      .tagline{
        color: $primary;
        font-weight: 700;
        margin: .4rem 0;
      }
      .stats {
        font-size: 1.5rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        margin: 1rem 0;
        color: $primary-dark;
        &__genre {
          font-weight: 700;
          font-size: 0.8rem;
          letter-spacing: 0.2rem;
          word-spacing: 0.3rem;
          text-transform: uppercase;
          margin-left: 1rem;
        }
        &__other {
          font-size: 1rem;
          font-weight: 700;
          letter-spacing: 0.2rem;
          &:not(:first-child) {
            margin-left: 1rem;
          }
        }
        @include for-phone-only{
          font-size: 1rem;
          margin: .5rem 0;
        }
      }
      .description {
        font-size: 1.2rem;
        letter-spacing: 0.1rem;
        word-spacing: 0.3rem;
        margin: 1rem 0;
      }

      .cast-wrapper {
        display: flex;
        flex-direction: column;
        margin: 1rem 0;
        .cast {
          display: flex;
          flex-direction: row;
          @include for-phone-only{
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            row-gap: 70px;
          }
        }
        .cast-title {
          font-size: 1.6rem;
          text-transform: uppercase;
          margin-bottom: .5rem;
          letter-spacing: 0.2rem;
        }

        &:not(:first-child){
          margin: 6rem 0;
        }
      }
    }
  }

}

.similar-movies{
  margin:0px 50px;

  @include for-phone-only{
    margin:0px 10px;
  }
}
