@import '../../../styles/colors';

.pagination {
  ul {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    align-items: center;

    li {
      padding: 12px 2px;
      margin: 0.5px;
      border-radius: 50%;
      width: 40px;
      text-align: center;

      span {
        font-weight: bold;
      }
    }

    .list-item:hover{
      cursor: pointer;
      background-color: $primary-dark;
      color: $dark;
    }

    .active {
      background: $primary;
      color: $dark;
    }
  }
}
